$active-color: #c0c0c0;

.text-animate {
    display: inline-block;
    opacity: 0;
    animation: bounceIn 1s 1s;
    animation-fill-mode: forwards;
    min-width: 10px;
}

.text-animate-hover {
    min-width: 10px;
    display: inline-block;
    animation-fill-mode: both;

    &:hover {
        animation: rubberBand 1s;
        color: $active-color;
    }
}



@for $i from 1 through 50 {
    .text-animate._#{$i} {
        animation-delay: #{$i / 20}s;
    }


}